@import url('https://fonts.googleapis.com/css2?family=Bellota+Text:wght@300;400;700&family=Montserrat:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500;600;700&display=swap');
.aboutb
{
    font-family: 'Bellota Text', cursive;
    font-family: 'Cairo', sans-serif;
    width: 100%;
    height: 100vh;
    background-color: #161922;
    display: flex;
    justify-content: center;
    align-items: center;
}
.aboutb .content img
{
    height: auto;
    width: 600px;
    max-width: 100%;
    border-radius: 90px;
}
.text
{
    width: 550px;
    max-width: 100%;
    padding: 0 10px;
}
.content
{
    width: 1280px;
    max-width: 95%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
.text h1
{
    color: #ffcc74;
    font-size: 85px;
    margin-bottom: 20px;
    text-transform: capitalize;
}
.text h5
{
    color: white;
    font-size: 25px;
    margin-bottom: 25px;
    text-transform: capitalize;
    letter-spacing: 1px;
}
.text p
{
    color: #c0c4d6;
    line-height: 28px;
    font-size: 18px;
    letter-spacing: 1px;
    margin-bottom: 45px;
}
.about_btn
{
    background-color: #ffcc74;
    color: black;
    border: 2px solid transparent;
    text-decoration: none;
    padding: 10px 30px;
    border-radius: 30px;
    font-weight: bold;
    font-size: 17px;
}
.about_btn:hover
{
    transform: scale(1.2);
    background-color: transparent;
    border: 2px solid #ffcc74;
    color: #ffcc74;
    transition: .4s ease-in-out;
    cursor: pointer;
}
.sci
{
    display: flex;
    gap: 1px;
    align-items: center;
    justify-content: center;
    margin-top: 25px;
}
.sci a
{
    color: #ffcc74;
    text-decoration: none;
    font-size: 30px;
    margin: 5px;
    width: 100%;
    height: auto;
    transition: 0.4s ease-in-out;
}
.sci a:hover
{
    color: #3e68d9;
}
@media screen and (max-width: 1180px)
{
    .aboutb
    {
        width: 100%;
        height: auto;
        padding: 70px 0;
    }
}
@media screen and (max-width: 650px)
{
    .aboutb .content img
    {
        margin-bottom: 35px;
    }
    .text h1
    {
        font-size: 60px;
        margin-bottom: 25px;
    }
}